import {
  getArticleComments,
  postArticleComments,
  deleteArticleComments,
} from "@/api/articles";
export default {
  name: "ArticleComments",
  props: {
    article: {
      type: Object,
      required: true,
    },
  },
  data: () => {
    return {
      comment: "",
      comments: [],
    };
  },
  methods: {
    async postComment() {
      const { data, status } = await postArticleComments(this.article.slug, {
        comment: {
          body: this.comment,
        },
      });
      if (status === 200) {
        this.comment = "";
        this.getArticleComments();
      }
    },
    async getArticleComments() {
      const { data } = await getArticleComments(this.article.slug);
      this.comments = data.comments;
    },
    async deleteArticleComments(article, comment) {
      const { data, status } = await deleteArticleComments(
        article.slug,
        comment.id
      );
      if (status === 200) {
        this.getArticleComments();
      }
    },
  },
  mounted() {
    this.getArticleComments();
  },
};
